<template>
  <div
    class="container col-6 mt-5 pt-5 bg-light"
    style="border-radius: 8px; min-width: 400px"
  >
    <h5 class="text-center">
      Create account to start using push-notifications
    </h5>
    <div class="container col-8 text-left mt-5">
      <div class="mb-3">
        <label for="loginInput" class="form-label">Login</label>
        <input v-model="login" type="email" class="form-control" id="loginInput" />
      </div>
      <div class="mb-3">
        <label for="passwordInput" class="form-label">Password</label>
        <input
          v-model="password"
          class="form-control"
          type="password"
          id="passwordInput"
          rows="3"
        />
      </div>
      <div class="mb-3">
        <button @click="registrate" class="btn btn-success sendBtn mb-5">Send</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions} from 'vuex';

export default {
  methods: {
      ...mapActions(["FETCH_REGISTRATION"]),
    async registrate() {
      const username = this.login;
      const password = this.password;
      this.FETCH_REGISTRATION({username, password});
    },
  },
  data(){
      return{
          login: "",
          password: ""
      }
  }
};
</script>

